<script>
import LayoutWebMobile from '@layouts/LayoutWebMobile'
import GxWizard from '@components/_CoreTaxes/Wizard/GxWizard'
import GxLedgerAccounts from '@components/_CoreTaxes/LedgerAccounts/GxLedgerAccounts.vue'
import GxLoader from '@components/_Core/Utils/GxLoader'
import GxCardModal from '@src/components/_Core/Cards/GxCardModal'
import { LEDGER_ACCOUNT_DELETE_ALL } from '@src/apollo/mutations'

import {
  GET_LEDGER_ACCOUNTS_PROCESS_STATUS,
  GET_LEDGER_ACCOUNTS,
} from '@src/apollo/queries'

export default {
  name: 'PreviewTemplate',
  components: {
    LayoutWebMobile,
    GxWizard,
    GxLedgerAccounts,
    GxLoader,
    GxCardModal,
  },
  data() {
    return {
      accountingFirmNumber: null,
      clientNumber: null,
      loading: false,
      loadingProcessed: true,
      error: null,
      steps: [
        'Elige tu catálogo',
        'Personaliza tu catálogo',
        'Periodo inicial',
        'Saldos iniciales',
        '¡Listo!',
      ],
      loadingSuccess: false,
      ledgerAccounts: null,
      process: null,
      processId: null,
      skipLedgerAccountsProcessStatus: true,
      skipLedgerAccounts: true,
      errorDescription: '',
      errorDescriptionTemplate: '',
      levels: null,
    }
  },
  props: {
    typeTemplateProps: {
      type: Number,
      default: 1,
      required: false,
    },
  },
  created() {
    this.clientNumber = localStorage.getItem('companyNumber')
    this.processId = localStorage.getItem('loadLedgerAccountProcessId')

    if (this.processId !== null) {
      this.skipLedgerAccountsProcessStatus = false
      this.loadingProcessed = true
    } else {
      this.loadingProcessed = true
      this.skipLedgerAccounts = false
    }
  },
  apollo: {
    getLedgerAccountsProcessStatus: {
      query: GET_LEDGER_ACCOUNTS_PROCESS_STATUS,
      variables() {
        return {
          processId: this.processId,
          clientNumber: [this.clientNumber],
        }
      },
      update(data) {
        this.process = null
        this.process = data.myAccountingFirms[0].clients[0].runningProcesses[0]
        this.accountingFirmNumber =
          data.myAccountingFirms[0].accountingFirmNumber
        this.skipLedgerAccountsProcessStatus = true
        //console.log(this.process.processStatus)
        switch (this.process.processStatus) {
          case 'processing':
          case 'notStarted':
            this.timeout = setTimeout(() => {
              this.skipLedgerAccountsProcessStatus = false
            }, 1500)
            this.$once('hook:destroyed', () => {
              clearTimeout(this.timeout)
            })
            break
          case 'finishedWithErrors':
            if (this.process.errors !== null) {
              this.errorDescription = ''
              this.process.errors.forEach((error) => {
                if (error.field !== 'ledger_account_file') {
                  this.errorDescription += error.errorMessage + ', '
                } else {
                  this.errorDescription = ''
                  this.errorDescriptionTemplate = error.errorMessage
                  return
                }
              })
            }
            // this.showLedgerAccounts()
            localStorage.removeItem('loadLedgerAccountProcessId')
            break
          case 'finishedOk':
            this.skipLedgerAccounts = false
            if (localStorage.getItem('redirectBack')) {
              localStorage.removeItem('redirectBack')

              this.$router.push({
                name: 'templates',
              })
            } else {
              //  this.showLedgerAccounts()
            }
            this.processId = null
            localStorage.removeItem('loadLedgerAccountProcessId')
            break
        }
        //console.log(this.process.processStatus)
      },
      skip() {
        return this.skipLedgerAccountsProcessStatus
      },
      fetchPolicy: 'no-cache',
    },
    getLedgerAccounts: {
      query: GET_LEDGER_ACCOUNTS,
      variables() {
        return {
          clientNumber: [this.clientNumber],
        }
      },
      update(data) {
        this.accountingFirmNumber =
          data.myAccountingFirms[0].accountingFirmNumber
        this.ledgerAccounts =
          data.myAccountingFirms[0].clients[0].accounting.chartOfAccounts.ledgerAccounts
        this.levels =
          data.myAccountingFirms[0].clients[0].accounting.chartOfAccounts.levels
        if (this.levels === 0 && this.processId === null) {
          this.$router.push({
            name: 'templates',
          })
        }
      },
      skip() {
        return this.skipLedgerAccounts
      },
      fetchPolicy: 'no-cache',
    },
  },
  methods: {
    async goBack() {
      let auxLedgerAccounts = this.ledgerAccounts
      try {
        this.loading = true
        this.loadingProcessed = true
        this.ledgerAccounts = null
        const response = await this.$apollo.mutate({
          mutation: LEDGER_ACCOUNT_DELETE_ALL,
          variables: {
            accountingFirmNumber: this.accountingFirmNumber,
            clientNumber: this.clientNumber,
          },
        })

        if (
          response.data.ledgerAccountDeleteAll.processID !== null &&
          response.data.ledgerAccountDeleteAll.error === null
        ) {
          this.skipLedgerAccountsProcessStatus = true
          this.processId = null
          this.processId = response.data.ledgerAccountDeleteAll.processID
          localStorage.setItem('loadLedgerAccountProcessId', this.processId)
          localStorage.setItem('redirectBack', true)
          this.skipLedgerAccountsProcessStatus = false
        } else {
          this.ledgerAccounts = auxLedgerAccounts
          this.errorDescription =
            response.data.ledgerAccountDeleteAll.error.message +
            ' - ' +
            response.data.ledgerAccountDeleteAll.error.details.join('')
          this.loading = false
        }
      } catch (error) {
        this.ledgerAccounts = auxLedgerAccounts
        this.errorDescription = this.$utils.parseServerError(error)
        console.log(this.errorDescription)
        this.loading = false
        return null
      }

      // this.$router.push({
      //   name: 'templates',
      // })
    },
    goNext() {
      this.$router.push({
        name: 'initialPeriod',
      })
    },
    closeModel() {
      if (
        this.errorDescription.indexOf(
          'No tienes cuentas contables para eliminar'
        ) > 0
      ) {
        this.errorDescription = ''
        localStorage.removeItem('redirectBack')
        this.processId = null
        this.$router.push({
          name: 'templates',
        })
      }
      this.errorDescription = ''
    },
    closeModalErrorTemplate() {
      this.errorDescriptionTemplate = ''
      this.processId = null
      this.$router.push({
        name: 'templates',
      })
    },
    finishRender() {
      this.loadingProcessed = false
    },
  },
}
</script>
<template>
  <LayoutWebMobile icon="keyboard_backspace" :icon-click-handler="goBack">
    <div class="flex flex-col w-full items-center px-8">
      <div class="flex flex-col text-xl mt-3"
        ><span class="font-black">Configuración Inicial</span></div
      >
      <div class="flex flex-col mt-10">
        <GxWizard :stepsProp="steps" :stepActiveProp="2" />
      </div>
    </div>
    <div class="flex flex-col w-full items-start px-8">
      <div class="flex flex-col text-base mt-12 text-left items-start"
        ><span class="font-black text-left items-start"
          >Personaliza tu catálogo de cuentas: agrega, quita o modifica las
          cuentas.</span
        >
      </div>
      <div class="flex flex-col w-full">
        <GxLoader v-if="this.loadingProcessed" class="mt-20 mb-5" />
      </div>
      <GxLedgerAccounts
        v-if="ledgerAccounts"
        class="mt-5 mb-15 w-full"
        :accountingFirmNumberProp="accountingFirmNumber"
        :clientNumberProp="clientNumber"
        :ledgerAccountsProp="ledgerAccounts"
        :editCatalog="true"
        :showCollapsed="true"
        :showArrowCollapse="true"
        :isInitial="true"
        @finishRender="finishRender"
      />
    </div>
    <div
      class="flex fixed w-full bottom-0 border-t h-19 shadow-8 bg-white items-stretch justify-between"
    >
      <div class="flex flex-col w-full left-0">
        <div class="flex fixed w-50 inline">
          <GxButton
            :variant="true"
            :ghost="true"
            class="ml-10 mt-4 rounded-md gx-button"
            @click="goBack"
            :disabled="this.loadingProcessed"
          >
            CANCELAR
          </GxButton>
        </div>
        <div class="flex fixed w-50 inline right-0">
          <GxButton
            :variant="true"
            class="mr-10 mt-4 gx-button"
            :loading-success="loadingSuccess"
            @click="goNext"
            :disabled="this.loadingProcessed"
          >
            Siguiente
          </GxButton>
        </div>
      </div>
    </div>
    <GxCardModal
      v-if="errorDescription"
      :headline="'Se presentaron algunos errores'"
      :caption="errorDescription"
      :card-img="$utils.alertImages.Oops"
      alt="Error"
      delayAnimation
    >
      <template v-slot:actions>
        <div class="flex justify-center mt-4">
          <GxButton @click="closeModel">ACEPTAR</GxButton>
        </div>
      </template>
    </GxCardModal>

    <GxCardModal
      v-if="errorDescriptionTemplate"
      :headline="'Error al importar el archivo'"
      :card-img="$utils.alertImages.Oops"
      alt="Error"
      delayAnimation
    >
      <template v-slot:htmlTag>
        <br />
        <a :href="errorDescriptionTemplate" download class="font-bold underline"
          >Descargar reporte de los errores de tu archivo de importación
        </a>
      </template>
      <template v-slot:actions>
        <div class="flex justify-center mt-4">
          <GxButton @click="closeModalErrorTemplate">ACEPTAR</GxButton>
        </div>
      </template>
    </GxCardModal>
  </LayoutWebMobile>
</template>
